.container {
  width: 320px;
  padding-left: 16px;
  padding-right: 16px;
  margin-left: auto;
  margin-right: auto;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 82px;
    padding-right: 82px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1200px;
    padding-left: 138px;
    padding-right: 138px;
  }
}
.window {
  margin: 0 auto;
  width: 500px;
  margin-top: 50px;
}
.window-body {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.window-body label {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.window input {
  width: 80px;
  margin-bottom: 5px;
}
.window .buttons {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 20px;
}
.window .buttons button {
  margin-right: 15px;
}
h1 {
  color: blue;
}
ul {
  list-style: none;
}
ul li {
  color: blue;
  font-weight: 700;
  font-size: 20px;
}
.result {
  margin: 0 auto;
  margin-top: 50px;
}
.result p:nth-child(1) {
  margin-left: 10px;
}
.result p:nth-child(2) {
  margin-left: 250px;
}
.result p:nth-child(3) {
  margin-left: 90px;
}
.result p:nth-child(4) {
  margin-left: 450px;
}
body {
  background-color: #dfdfdf;
}
button {
  cursor: pointer;
}
